.bj-rules {
  padding: 20px 30px 50px;
  min-height: 400px;

  .bj-loader-container {
    width: 100%;
    height: 100%;
  }

  .bj-rule:not(:last-child) {
    margin-bottom: 50px;
  }
}

@media only screen and (max-width: 480px) {
  .bj-rules {
    padding: 20px 20px 50px;
  }
}
